@import "style/mixins.module.scss";

.root {
    width: 100%;
    padding: 0;
    border: none;
}

.content {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 16px;
    background: get_color("gray", "-5");
    font-family: Inter, sans-serif;
    cursor: pointer;
    @include color("gray", "-4");

    @include respond-to("laptop") {
        padding: 4px 0;
    }
}

.text {
    text-align: center;
    margin: 0 8px;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        margin: 0 8px 0 0;
        font-size: 22px;
        line-height: 32px;
    }
}
