@import "style/mixins.module.scss";

.title {
    margin-bottom: 16px;

    @include respond-to("laptop") {
        margin-bottom: 32px;
        width: 800px;
        margin-right: auto;
        margin-left: auto;
        text-align: center;
    }
}

.benefits-flex-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15px;

    @include respond-to("laptop") {
        gap: 40px;
    }
}

.benefits-group {
    display: grid;
    gap: 16px;

    @include respond-to("laptop") {
        grid-template-columns: 1fr 1fr;
        gap: 40px;
    }
}

.benefits-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin: 0;
    padding: 0;
    width: 100%;

    @include respond-to("laptop") {
        gap: 40px;
    }
}

.benefit-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 16px;
    background: rgba(242, 242, 242, 0.5);
    border-radius: 16px;

    &:last-child {
        margin-bottom: 0;
    }

    @include respond-to("laptop") {
        padding: 16px 56px;
        margin-bottom: 0;

        &:nth-child(1) {
            grid-area: firstItem;
        }

        &:nth-child(2) {
            grid-area: secondItem;
        }

        &:nth-child(3) {
            grid-area: thirdItem;
        }

        &:nth-child(4) {
            grid-area: fourthItem;
        }
    }
}

.benefit-item-header {
    margin-bottom: 9px;
}

.benefit-item-description {
    margin-bottom: 0;
    list-style-type: none;
    font-size: 17px;
    line-height: 24px;
    text-align: center;

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
    }
}
