@import "style/mixins.module.scss";

.root {
    overflow-x: hidden;
}

.head {
    @include respond-to("mobile") {
        margin-bottom: 80px;
    }
}

.check-up,
.container,
.how-it-works {
    margin-bottom: 120px;

    @include respond-to("desktop") {
        margin-bottom: 240px;
    }
}

.how-it-works {
    @include respond-to("laptop") {
        padding-left: 40px;
        padding-right: 40px;
    }

    @include respond-to("container-default") {
        width: 1288px;
        margin-left: auto;
        margin-right: auto;
    }
}

.appointment {
    margin-bottom: 120px;

    @include respond-to("laptop") {
        margin-bottom: 144px;
    }

    @include respond-to("desktop") {
        margin-bottom: 200px;
    }
}

.doctors {
    margin-bottom: 64px;

    @include respond-to("laptop") {
        margin-bottom: 72px;
    }

    @include respond-to("desktop") {
        margin-bottom: 140px;
    }
}

.prices {
    margin-bottom: 120px;

    @include respond-to("desktop") {
        margin-bottom: 240px;
    }
}

.appointment-title {
    @include formTitle();
}
