@import "style/mixins.module.scss";
@import "./animateTexts.module.scss";
@import "./animateImages.module.scss";

.root {
    position: relative;

    @include respond-to("laptop") {
        padding-bottom: 137px;
    }

    @include respond-to("desktop") {
        padding-bottom: 257px;
    }
}

.header {
    margin-bottom: 48px;

    @include respond-to("laptop") {
        margin-bottom: 64px;
    }
}

.title {
    text-align: left;
    margin-bottom: 16px;
    position: relative;
    display: none;

    @include color("gray", "-4");

    @include respond-to("laptop") {
      display: block;
        text-align: center;
        margin-bottom: 64px;
        width: 800px;
        margin-left: auto;
        height: 128px;
        margin-right: auto;
    }

    @include respond-to("desktop") {
        margin-bottom: 73px;
    }

    &__first-text {
        margin-bottom: 16px;
    }

    &__text {
      text-align: left;
      position: absolute;
      width: 100%;
      left: 0;
      top: 0;
      animation-duration: 16s;
      animation-iteration-count: infinite;
      animation-fill-mode: forwards;
      animation-timing-function: cubic-bezier(0.42, 0, 0, 1);
      animation-delay: 1s;

      @include respond-to("laptop") {
          text-align: center;
      }

      &:first-child {
          animation-name: fadeUp1;
      }

      &:nth-child(2) {
          animation-name: fadeUp2;
          transform: translate3d(0, -50%, 0);
          opacity: 0;
      }

      &:nth-child(3) {
          animation-name: fadeUp3;
          transform: translate3d(0, -50%, 0);
          opacity: 0;
      }

      &:nth-child(4) {
          animation-name: fadeUp4;
          transform: translate3d(0, -50%, 0);
          opacity: 0;
      }
    }
}

.title-mobile {
    margin-bottom: 0;
    position: relative;
    display: block;
    height: 152px;

    @include respond-to("laptop") {
        display: none;
    }
}

.container {
    position: relative;

    @include respond-to("laptop") {
        display: flex;
        justify-content: flex-end;
        flex-direction: row-reverse;
        align-items: center;
    }
}


.container-images {
    position: relative;
    height: calc(100vw - 48px);
    margin-bottom: 80px;
    max-height: 600px;

    width: 100%;
    left: calc(50%);
    transform: translateX(-50%);

    @include respond-to("laptop") {
        margin-left: 0;
        margin-right: 0;
        left: auto;
        transform: none;
        width: 460px;
        height: 488px;
        max-height: auto;
        margin-bottom: 0;
    }

    @include respond-to("desktop") {
        width: 624px;
        height: 663px;
    }

    picture {
        height: 100%;
    }
}

.container-text {
    @include respond-to("laptop") {
        width: 300px;
        margin-right: 82px;

        @include color("gray", "-4");
    }
}

.price-per-month {
    @include respond-to("laptop") {
        margin-bottom: 16px;
    }
}

.subtitle {
    display: none;

    @include respond-to("laptop") {
        @include color("gray", "-4");
        margin-bottom: 16px;
        display: block;
    }

    &_mobile {
      display: block;
      
      @include respond-to("laptop") {
          display: none;
      }
    }
}


.image {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    transform-origin: 25% bottom;
    animation-duration: 16s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.42, 0, 0, 1);
    object-fit: cover;
    z-index: auto;

    @include respond-to("laptop") {
        width: 460px;
        height: 488px;
    }

    @include respond-to("desktop") {
        width: 624px;
        height: 663px;
        transform-origin: 22% bottom;
    }

    &_1 {
        transform: rotate(15deg) translateX(100px);
        opacity: 0.3;
        animation-name: fadeImage1;

        @include respond-to("desktop") {
            transform: rotate(15deg) translateX(72px);
        }
    }

    &_2 {
        transform: rotate(10deg) translateX(100px);
        opacity: 0.5;
        animation-name: fadeImage2;

        @include respond-to("desktop") {
            transform: rotate(10deg) translateX(72px);
        }
    }

    &_3 {
        transform: rotate(5deg) translateX(100px);
        opacity: 0.8;
        animation-name: fadeImage3;

        @include respond-to("desktop") {
            transform: rotate(5deg) translateX(72px);
        }
    }

    &_4 {
        transform: rotate(0) translateX(0);
        opacity: 1;
        animation-name: fadeImage4;
    }
}

.container-price {
    position: absolute;
    left: 8px;
    top: 8px;
    z-index: 10;

    &__value {
        padding: 6px 8px;
        background-color: get_color("primary", "red");
        margin-bottom: 4px;

        @include color("gray", "0");

        @include respond-to("laptop") {
            padding: 0;
            margin-bottom: 0;
            margin-right: 8px;
            background-color: transparent;

            @include color("gray", "-4");
        }
    }

    &__text {
        font-size: 13px;
        line-height: 20px;

        @include color("gray", "0");

        @include respond-to("laptop") {
            font-size: 17px;
            line-height: 24px;

            @include color("gray", "-4");
        }
    }

    @include respond-to("laptop") {
        position: static;
        display: flex;
        align-items: center;
        padding: 6px 8px;
        border: 1px solid get_color("gray", "-4");
        width: fit-content;
        margin-bottom: 16px;
    }
}

.button {
    display: none;

    @include respond-to("laptop") {
        display: inline-block;
    }
}
