@import "style/mixins.module.scss";

.main-content,
.brackets-vs-impress {
    display: flex;
    flex-direction: column;

    @include respond-to("laptop") {
        flex-direction: row;
        min-height: 780px;
    }
}

.brackets-vs-impress {
    display: flex;
    flex-direction: column;

    @include respond-to("laptop") {
        flex-direction: row;
        min-height: 780px;
    }
}

.content {
    padding: 40px 24px;
    background: #fdefef;

    @include respond-to("laptop") {
        width: 50%;
        display: flex;
        flex-direction: column;
        padding: 94px 110px;
    }
}

.title {
    margin-bottom: 24px;
    font-size: 36px;
    line-height: 40px;

    @include respond-to("desktop") {
        font-size: 56px;
        line-height: 64px;
    }

    @include respond-to("laptop") {
        margin-bottom: 32px;
    }
}

.list {
    @include respond-to("laptop") {
        margin-bottom: 40px;
    }
}

.item {
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
    }
}

.picture {
    @include respond-to("laptop") {
        width: 50%;
    }
}

.image {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
}

.brackets-vs-impress {
    flex-direction: row;

    @include respond-to("laptop") {
        min-height: 480px;
    }
}

.brackets-content,
.impress-content {
    position: relative;
    width: 50%;
}

.badge {
    position: absolute;
    top: 16px;
    left: 16px;
    padding: 4px 8px;
    border-radius: 4px;
    text-transform: uppercase;
    font-size: 13px;
    line-height: 20px;
    color: #262d33;

    &_has-accent {
        @include color("primary", "red");
    }

    @include respond-to("laptop") {
        font-size: 17px;
        line-height: 24px;
    }
}

.badge {
    background-color: get_color("gray", "0");
}
