@keyframes fadeUp1 {
  0%, 10% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }

  13% {
    opacity: 0;
  }

  20%, 70% {
    opacity: 0;
    transform: translate3d(0, -50%, 0);
  }

  71% {
    transform: translate3d(0, 50%, 0);
    opacity: 0;
  }

  80%, 100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeUp2 {
  0%, 10% {
    transform: translate3d(0, 50%, 0);
    opacity: 0;
  }

  20%, 30% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }

  33% {
    opacity: 0;
  }

  40%, 100% {
    opacity: 0;
    transform: translate3d(0, -50%, 0);
  }
}

@keyframes fadeUp3 {
  0%, 30% {
    transform: translate3d(0, 50%, 0);
    opacity: 0;
  }

  40%, 50% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }

  53% {
    opacity: 0;
  }

  60%, 100% {
    opacity: 0;
    transform: translate3d(0, -50%, 0);
  }
}

@keyframes fadeUp4 {
  0%, 50% {
    transform: translate3d(0, 50%, 0);
    opacity: 0;
  }

  60%, 70% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }

  73% {
    opacity: 0;
  }

  80%, 100% {
    opacity: 0;
    transform: translate3d(0, -50%, 0);
  }
}