@import "style/mixins.module.scss";

.root {
    list-style: none;
    padding: 0;
    margin: 0;
}

.item {
    position: relative;
    margin-bottom: 16px;
    font-size: 24px;
    line-height: 32px;
    padding-left: 40px;

    &:last-child {
        margin-bottom: 0;
    }
}

.item-text {
    display: inline-block;
    margin-bottom: 0;
}

.icon {
    color: get_color("primary", "red");
    position: absolute;
    left: 0;
    top: 0;
}
