@keyframes fadeImage4 {
  0%, 21% {
    transform: rotate(0) translateX(0);
    opacity: 1;
    z-index: 4;
  }

  25% {
    transform: rotate(-10deg) translateX(10px);
    opacity: 0; 
    z-index: 1;
  }

  28% {
    transform: rotate(15deg) translateX(30px) translateY(10px);
    opacity: 0;
    z-index: 1;
  }

  33%, 40% {
    transform: rotate(15deg) translateX(100px) translateY(0);
    opacity: 0.3;
    z-index: 1;
  }

  50%, 60% {
    transform: rotate(10deg) translateX(100px);
    opacity: 0.5;
    z-index: 2;

    @include respond-to("desktop") {
      transform: rotate(10deg) translateX(72px);
    }
  }

  70%, 80% {
    transform: rotate(5deg) translateX(100px);
    opacity: 0.8;
    z-index: 3;

    @include respond-to("desktop") {
      transform: rotate(5deg) translateX(72px);
    }
  }

  90%, 100% {
    transform: rotate(0) translateX(0);
    opacity: 1;
    z-index: 4;
  }
}

